.App {
  /* text-align: center; */
  background-color: #383c44;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: calc(10px + 1vmin);
  color: #aaa;
