.LinkStyle {
  color: #aaa;
  font-weight: bold;
  text-decoration: none;
  font-size: calc(8px + 1vmin);
}

.external {
  color: rgb(72, 186, 224);
}

.icon {
  color: white;
}

/* Source: https://codepen.io/tomhodgins/pen/gPaPoq */

blockquote {
  margin: 0 auto;
  padding: 1em;
  border-left: 5px solid #777;
}
blockquote:before {
  display: none;
}
blockquote:not(:first-of-type) {
  margin-top: .5em;
}
blockquote p {
  color: #aaa;
  font-size: 12pt;
  line-height: 1.4;
  font-family: 'PT Serif', Cambria, 'Hoefler Text', Utopia, 'Liberation Serif', 'Nimbus Roman No9 L Regular', Times, 'Times New Roman', serif;
}
blockquote footer {
  margin-top: .5em;
  padding: 0;
  color: #999;
  font-size: 12pt;
  text-align: left;
  font-style: italic;
}
blockquote footer:before {
  content: '— ';
}
blockquote:nth-of-type(even) {
  text-align: right;
  border-left: none;
  border-right: 5px solid #777;
}
blockquote:nth-of-type(even) footer {
  text-align: right;
}
blockquote:nth-of-type(even) footer:before {
  content: '';
}
blockquote:nth-of-type(even) footer:after {
  content: ' —';
}

